.timeline {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    /* font-size: 13px; */
    padding: 2px 3px;
    font-weight: bold;

    font-size: 15px;
    line-height: 40px;

    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}
.animatedTimeline {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    /* font-size: 13px; */
    padding: 2px 3px;
    font-weight: bold;

    font-size: 15px;
    line-height: 40px;

    animation-name: loading20;
    animation-duration: 2s;

    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}
.animatedTimeline30 {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    /* font-size: 13px; */
    padding: 2px 3px;
    font-weight: bold;

    font-size: 15px;
    line-height: 40px;

    animation-name: loading30;
    animation-duration: 2s;

    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}

.timelineNoPadding {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    /* font-size: 13px; */
    padding: 2px 0px;
    font-weight: bold;

    font-size: 15px;
    line-height: 40px;

    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}
.animatedTimelineNoPadding {
    background-color: rgb(31, 91, 221);
    color: white;
    text-align: center;
    margin-bottom: 20px;
    /* font-size: 13px; */
    padding: 2px 0px;
    font-weight: bold;

    font-size: 15px;
    line-height: 40px;

    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);

    animation-name: loading30;
    animation-duration: 2s;
}

