.App {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}
  
  a {
    text-decoration: none;
  }

  .support-page {
    padding: 7px;
  }
  .button-paypal {
    margin: 100px 0px 150px 0px;
    display: flex;
    justify-content: center;
  }
  .donate-button {
    background-color: #2997D8;
    color: white;
    font-weight: bold;
    padding: 20px;
    border-radius: 25px;
    font-size: 30px;
    width: 200px;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  }
  .donate-button:hover {
    background-color: #45a3da;
  }
  .support-socials {
    margin: 40px 0px 100px 0px;
    display: flex;
    align-items: center;
  }

  .thankyoupage {
    margin-bottom: 50px;
  }
  .thankyoupage-more {
    margin-bottom: 100px;
  }